"use client";

import { useState } from "react";
import { Card } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { ScrollArea } from "@/components/ui/scroll-area";
import { useConversatie, useTrimiteMesaj } from "@/hooks/useApi";

interface Mesaj {
  tip: 'utilizator' | 'asistent';
  text: string;
  timestamp: string;
}

const AsistentVirtual = () => {
  const [mesajNou, setMesajNou] = useState('');
  const { data: conversatieData, isLoading } = useConversatie();
  const { mutate: trimiteMesaj, isPending: trimiteLoading } = useTrimiteMesaj();

  const mesaje: Mesaj[] = conversatieData?.data?.conversatie || [];

  const handleTrimiteMesaj = async () => {
    if (!mesajNou.trim() || trimiteLoading) return;

    trimiteMesaj(
      { mesaj: mesajNou },
      {
        onSuccess: () => {
          setMesajNou('');
        },
        onError: (error) => {
          console.error('Eroare la trimiterea mesajului:', error);
        }
      }
    );
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleTrimiteMesaj();
    }
  };

  if (isLoading) {
    return (
      <div className="flex justify-center items-center min-h-[400px]">
        <p className="text-gray-500">Se încarcă conversația...</p>
      </div>
    );
  }

  return (
    <Card className="flex flex-col h-[600px]">
      <div className="p-4 border-b">
        <h2 className="text-xl font-semibold">Asistent Virtual BT Go</h2>
        <p className="text-sm text-gray-500">Disponibil 24/7 pentru întrebările dumneavoastră</p>
      </div>

      <ScrollArea className="flex-1 p-4">
        <div className="space-y-4">
          {mesaje.map((mesaj, index) => (
            <div
              key={index}
              className={`flex ${mesaj.tip === 'utilizator' ? 'justify-end' : 'justify-start'}`}
            >
              <div
                className={`max-w-[80%] p-3 rounded-lg ${
                  mesaj.tip === 'utilizator'
                    ? 'bg-blue-600 text-white'
                    : 'bg-gray-100 text-gray-900'
                }`}
              >
                <p>{mesaj.text}</p>
                <span className="text-xs opacity-70">
                  {new Date(mesaj.timestamp).toLocaleTimeString('ro-RO')}
                </span>
              </div>
            </div>
          ))}
          {trimiteLoading && (
            <div className="flex justify-start">
              <div className="bg-gray-100 p-3 rounded-lg">
                <p className="text-gray-500">Asistentul scrie...</p>
              </div>
            </div>
          )}
        </div>
      </ScrollArea>

      <div className="p-4 border-t">
        <div className="flex gap-2">
          <Input
            placeholder="Scrieți mesajul dvs. aici..."
            value={mesajNou}
            onChange={(e) => setMesajNou(e.target.value)}
            onKeyPress={handleKeyPress}
            disabled={trimiteLoading}
          />
          <Button 
            onClick={handleTrimiteMesaj}
            disabled={trimiteLoading || !mesajNou.trim()}
          >
            Trimite
          </Button>
        </div>
      </div>
    </Card>
  );
};

export default AsistentVirtual;
